<template>
  <div class="catalogs-map m-0 p-0">
    <ul class="list-group">
      <li class="p-2" v-for="(c, index) in TestList" :key="index">
        <a
          class="link link-light m-0"
          data-bs-toggle="collapse"
          :href="'#collapseExample' + index"
          role="button"
          aria-expanded="false"
          :aria-controls="'#collapseExample' + index"
          >{{ c.title }}</a
        >
        <div class="collapse" :id="'collapseExample' + index">
          <Project v-for="(p, index) in c.children" :key="index" :data="p" />

          <div class="null-children" v-if="c.children.length == 0">
            Каталог в стадии наполнения...
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
import Project from "./Project.vue";
import { mapActions } from "vuex";
import { mapGetters } from "vuex";
export default {
  components: { Project },
  mounted() {
    this.FetchCatalogs();
    this.FetchProjects();
  },
  methods: {
    ...mapActions(["FetchCatalogs", "FetchProjects", "Capabilities"]),
  },
  computed: {
    ...mapGetters(["catalogs", "projects"]),
    TestList() {
      const c = this.catalogs.map((c) => {
        return { title: c.title, id: c._id, children: [] };
      });

      const lc = c.length;
      const lp = this.projects.length;

      for (let i = 0; i < lc; i++) {
        for (let j = 0; j < lp; j++) {
          if (c[i].id == this.projects[j].section) {
            c[i].children.push(this.projects[j]);
          }
        }
      }

      return c;
    },
  },
};
</script>

