<template>
  <vl-map
    ref="map"
    data-projection="EPSG:3857"
    :load-tiles-while-animating="true"
    :load-tiles-while-interacting="true"
    :controls="false"
    @click="Click"
    class="map"
  >
    <vl-view :zoom="zoom" :center="center"></vl-view>

    <vl-layer-tile>
      <vl-source-xyz
        :url="baseMaps[currentBaseMap].url"
        cross-origin="none"
        SameSite="none"
      ></vl-source-xyz>
    </vl-layer-tile>

    <vl-layer-image v-for="(project, index) in projectsToDisplay" :key="index">
      <vl-source-image-wms
        v-if="capabilities[project._id]"
        :layers="capabilities[project._id]"
        :url="`http://qgisserver.petyaogurkin.keenetic.pro/ogc/${project.map}`"
        :crossOrigin="'anonymous'"
      ></vl-source-image-wms>
    </vl-layer-image>

    <vl-overlay
      projection="EPSG:3857"
      :position="currentPosition"
      :auto-pan="true"
      v-if="currentPosition"
    >
      <template>
        <div class="overlay_videoclip bg-white text-black">
          <a class="link" @click="OpenInfo"> Подробнее... </a>
        </div>
      </template>
    </vl-overlay>
  </vl-map>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  data() {
    return {
      currentPosition: undefined,
      zoom: 3,
      center: [0, 0],
    };
  },
  computed: {
    ...mapGetters([
      "baseMaps",
      "currentBaseMap",
      "projectsToDisplay",
      "capabilities",
    ]),
  },
  methods: {
    OpenInfo() {
      this.$store.dispatch("Open");
    },

    Click(e) {
      const map = this.$refs.map;
      const coordinates = map.getCoordinateFromPixel(e.pixel);
      const viewResolution = map.getView().getResolution();
      const viewProjection = map.getView().getProjection();

      map.forEachLayerAtPixel(e.pixel, (layer) => {
        if (layer.type == "IMAGE") {
          const url = layer
            .getSource()
            .getGetFeatureInfoUrl(coordinates, viewResolution, viewProjection, {
              INFO_FORMAT: "application/json",
            });

          fetch(url)
            .then((e) => {
              return e.text();
            })
            .then((e) => {
              console.log(e);
            });
        }
      });
    },
  },
};
</script>

<style>
.map {
  width: 100%;
  height: 100%;
}

.overlay_videoclip {
  opacity: 1;
  padding: 5px;
  position: absolute;
  color: white;
  text-align: center;
  border-radius: 6px;
  transform: translate(-50%, -110%);
  width: 300px;
  word-break: break-all;
}
</style>